.initPage{
        min-height: 100%;
        width: 100%;
        background-color: rgb(33, 37, 40);
        color: white;
        display: flex;
}

.initPageContent{
    width:100%;
    margin: 16px;
    background-color: #2a2e31;
    border-radius: 8px;
    padding: 24px;
    display: flex;
    flex-direction: column;
    
}

.initPageTrainingAge{
    display: flex;
    align-items: center;
    gap: 24px;
}

.initPageTrainingAgeChooser{
    max-width: 250px;
}

.initTrainingAgeRec{
    margin: 10px 0 10px 0;
    color:rgb(148, 148, 148)
}

.initContinue{
    display: flex;
    gap: 32px;
}

.initValidation{
    margin:0;
    color: rgb(255, 187, 0);
}