.workout{
    flex-grow: 1;
    background-color: rgb(42, 46, 49);
    margin: 10px 5px;
    border-radius: 20px;
    padding: 20px;
    overflow: auto;
    position: relative;
    min-width: 285px;
    display:flex;
    flex-direction: column;
}

.workoutAdd{
  position: absolute;
  top: 20px;
  right: 20px;
  height: 32px;
  width: 32px;
}

::-webkit-scrollbar {
    width: 10px;
  }
  
  ::-webkit-scrollbar-track {
    background: #464646; 
  }
   
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

.workoutCaption{
    margin: 0px 0px 10px 0px;
    
}

.workoutExerciseFiller{
  margin: 8px;
  height: 100%;
}