:root { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  :root { font-family: 'Inter var', sans-serif; }
}

.landingPage{
    height: 100%;
    width: 100%;
    background-color: rgb(33, 37, 40);
    color: white;
    display: flex;
}

.landingPageContent{
    width:100%;
    margin: 16px;
    background-color: #2a2e31;
    border-radius: 8px;
    display: flex;
}

.landingPageLeftContent{
    display: flex;
    flex-flow: column;
    padding: 12px 20px 0 20px;
    margin: 0;
    width: 100%;
}

main {
    flex: 1;
}

.landingPageCaption{
    font-weight:normal;
    color: #5fcbff;
    letter-spacing: 0px;
    font-weight: 400;
    font-size:6em;
    margin: 0;
    padding: 0;
}

.landingPageSubCaption{
    font-weight:100;
    letter-spacing: 0px;
    font-size:2em;
    margin: -24px 0 24px 8px ;
    padding: 0;
}



main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 16px;
    margin-top: -100px;
}

.landingPageIntroduction{
    font-weight:normal; 
    font-size: 2.25em; 
    margin: 0;
}

p {
    font-size: 1.35em; 
}

footer {
    margin: 0 0 8px 0;
}


@media only screen and (max-width: 600px) {
    .landingPageRightContent {
        display: none;
    }
}

.landingPageImage{
    height: 100%;
    width: 100%;
    clip-path: polygon(0 0,100% 0,100% 100%,354.00px 100%,0 calc(100% - 800.00px));
    border-radius: 0 8px 8px 0;  
}

b {
    font-weight: 900;
}