.dataEditer{
    background-color: rgb(21, 21, 21);
    color: white;

    width: 375px;
    max-height: 730px;
    padding: 12px 24px;
    border-radius: 12px;
}

.dataEditerCaption{
    margin:0;
    font-weight:bold;
    
}

.dataEditerDescription{
    margin:12px 0 4px 0;
    font-size: medium;
    font-weight: normal;
}

.volumeData{
    display: flex;
}

.volumeDataInput{
    font-size: small;
    text-align: center;
}

.volumeDataInputSeperators{
    margin: 0 32px;
}

.muscleGroups{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 8px
}
.muscleGroups>* {
    flex: 1 1 33.3333%;
}

.Reccomondations{
    display:flex;
    justify-content: flex-start;
    gap: 16px
}

.ReccomondationsText{
    margin:12px 0 4px 0;
    font-size:medium;
    font-weight: normal;
    color: rgb(97, 97, 97);
}

.dataEditerValidation{
    margin:0;
    font-size: medium;
    font-weight: normal;
    color: rgb(255, 187, 0);
}

.dataEditerButtons{
    display:flex;
    gap: 16px;
}

.dataEditerHead{
    margin:0;
    display:flex;
    align-items: baseline;
    justify-content: space-between;
}
