.analitics{
    background-color: rgb(42, 46, 49);
    margin: 10px 5px;
    border-radius: 20px;
    padding: 20px;
    
    flex-grow: 4;
    position: relative;
    overflow: auto;
}

.caption{
    margin: 12px 0px 0px 0px; 
}

.actions{
    display: flex;
}


.inputFile{
    display: none;
}

.captionDiagram {
    margin: 0;
    padding: 0;
}

.react-tabs__tab-list {
    display:flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0;
    padding: 0;
  }

.workoutTabs {
    display:flex;
    justify-content: flex-start;

}

  .react-tabs__tab {
    list-style: none;
    cursor: pointer;
    margin: 0;
    padding: 10px;
    background: #444a4e;
    border-radius: 8px;
    margin: 0 4px 0 0;
  }

  .react-tabs__tab--selected {
    background: #fff;
    color: black;
  }