.sidebar{
    background-color: rgb(42, 46, 49);
    margin: 10px 5px;
    border-radius: 20px;
    padding: 20px;
    overflow: auto;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
}

.sidebar_icon{
    width:40px;
    height: auto;
    margin-bottom: 24px;
}

.sidebar_actions{
    display: flex;
    flex-direction: column;
    gap: 16px;
}